
html,body,a,img,dl,dt,dd,div,fieldset,legend,table,thead,th,tr,td,tbody,li,ol,ul,form,section,article,span,em,
figure,figcaption,h1,h2,h3,h4,h5,h6,p{margin:0;padding:0;font-family:Dotum,'\B3CB\C6C0',Helvetica,"Apple SD Gothic Neo",sans-serif;
}
img, form, fieldset{border:0
}
li{list-style: none
}
html,body{height:100%
}

#warp {
  margin: 0 auto;
}
#warp h1 {
    background: #ffe11d;
    text-align: center;
    height: 58px;
    line-height: 58px;
    font-size: 28px;
    letter-spacing: 1px;
}
#warp .v_img {
    max-width: 570px;
    margin: 40px auto 30px;
    text-align: left;
    padding: 0 20px;
}
@media screen and (max-width: 570px) {
#warp .v_img img {
        width: 100%;
}
}
#warp .info {
    background: #ffe11d;
    height: 58px;
    line-height: 58px;
    margin-bottom: 40px;
}
#warp .info h2 {
      max-width: 570px;
      margin: 0 auto;
      font-size: 24px;
      font-weight: normal;
      padding: 0 20px;
}
#warp .iptArea {
    max-width: 570px;
    margin: 0 auto;
    padding: 0 20px;
}
#warp .iptArea .topNotice {
      font-size: 16px;
      margin-bottom: 7px;
}
#warp .iptArea .topNotice strong {
        font-size: 20px;
}
#warp .iptArea fieldset {
      border: 6px solid #f5f5f5;
      -webkit-box-sizing: border-box;
              box-sizing: border-box;
      padding: 20px 30px;
      margin-bottom: 30px;
}
#warp .iptArea fieldset dl {
        overflow: hidden;
}
#warp .iptArea fieldset dl dt {
          display: block;
          float: left;
          clear: left;
          font-size: 19px;
          color: #666;
          height: 30px;
          line-height: 30px;
}
#warp .iptArea fieldset dl dd {
          padding-left: 100px;
          margin-bottom: 20px;
}
#warp .iptArea fieldset dl dd input {
            background: #f5f5f5;
            height: 30px;
            border: 0;
            width: 100%;
            padding: 10px;
            -webkit-box-sizing: border-box;
                    box-sizing: border-box;
            color: #666;
            font-size: 16px;
}
#warp .iptArea fieldset #submitBtn {
        display: inline-block;
        background: #ffe11d;
        width: 100%;
        border: 0;
        height: 40px;
        font-size: 20px;
        font-weight: bold;
        line-height: 40px;
}
#warp .iptArea fieldset .desc {
        font-size: 14px;
        color: #9d9d9d;
        margin-top: 10px;
}
#warp .inNotice {
    padding: 0 20px;
    background: #f5f5f5;
    height: 58px;
    line-height: 58px;
    margin-bottom: 20px;
    font-weight: normal;
}
#warp .inNotice p {
      width: 570px;
      margin: 0 auto;
}
#warp .boxList {
    max-width: 570px;
    margin: 0 auto;
    padding: 0 20px;
}
#warp .boxList ul {
      display: -webkit-box;
      display: -ms-flexbox;
      display: flex;
      -webkit-box-pack: justify;
          -ms-flex-pack: justify;
              justify-content: space-between;
      -ms-flex-wrap: wrap;
          flex-wrap: wrap;
}
#warp .boxList ul li {
        width: 180px;
        height: 116px;
        margin-bottom: 10px;
        border: 1px solid #f5f5f5;
}
#warp .boxList ul li.noneBorder {
          border: none;
}
#warp .boxList ul li dl {
          padding: 10px;
          border-top: 4px solid #ffe11d;
}
#warp .boxList ul li dl dt {
            color: #000;
            font-size: 16px;
            margin-bottom: 10px;
}
#warp .boxList ul li dl dt em {
              display: inline-block;
              margin-right: 5px;
              font-weight: bold;
              font-size: 17px;
              color: #d26402;
              font-style: normal;
}
#warp .boxList ul li dl dd {
            font-size: 14px;
            color: #9d9d9d;
}
#warp .boxList ul li dl dd strong {
              color: #373737;
              font-weight: normal;
}




































































